<template><section><h3>demo Alert <code>type</code><code>info</code></h3>
<template>
    <y-button type="ghost" @click.native="test1">test</y-button>
    <y-button type="ghost" @click.native="test2">icon</y-button>
    <y-button type="ghost" @click.native="test3">icon</y-button>
    <y-button type="ghost" @click.native="test4">icon</y-button>
    <y-button type="ghost" @click.native="test5">icon</y-button>
    <y-button type="ghost" @click.native="test6">icon</y-button>
    <y-button type="ghost" @click.native="test7">icon,</y-button>
</template>
  
<h2></h2>
<pre><code class="language-javacript">this.$notify({
    &quot;type&quot;: &quot;danger&quot;,
    &quot;title&quot;:&quot;test&quot;,
    &quot;content&quot;:&quot;sfafasdf&quot;,
    &quot;icon&quot;:true,
    &quot;onClose&quot;:()=&gt;{
        console.log(1)
    }
})
</code></pre>
<table>
<thead>
<tr>
<th></th>
<th></th>
<th></th>
<th></th>
<th></th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td></td>
<td><code>String</code></td>
<td><code>success</code></td>
<td><code>info</code>,<code>warning</code>,<code>danger</code></td>
</tr>
<tr>
<td>title</td>
<td></td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td></td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>onClose</td>
<td></td>
<td><code>Function</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>icon</td>
<td>icon</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
<td>-</td>
</tr>
<tr>
<td>time</td>
<td></td>
<td><code>Number</code></td>
<td><code>0(,)</code></td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    methods:{
        test1(){
            this.$notify("test")
        },
        test2(){
            this.$notify({
                "title":"test",
                "content":"sfafasdf",
                "icon":true
            })
        },
        test3(){
            this.$notify({
                "title":"test",
                "content":"sfafasdf",
                "icon":true, 
                "time":1000
            })
        },
        test4(){
            this.$notify({
                "type": "success",
                "title":"test",
                "content":"sfafasdf",
                "icon":true
            })
        },
        test5(){
            this.$notify({
                "type": "info",
                "title":"test",
                "content":"sfafasdf",
                "icon":true
            })
        },
        test6(){
            this.$notify({
                "type": "warning",
                "title":"test",
                "content":"sfafasdf",
                "icon":true
            })
        },
        test7(){
            this.$notify({
                "type": "danger",
                "title":"test",
                "content":"sfafasdf",
                "icon":true
            })
        },
        test7(){
            this.$notify({
                "type": "danger",
                "title":"test",
                "content":"sfafasdf",
                "icon":true,
                "onClose":()=>{
                    console.log(1)
                }
            })
        }
    }
}
</script>


